<template>
  <div class="column pd-top">
	<uc-cell v-for="(item,index) of list"
	:key="item._id"
	:data="item"
	collectionName="column"
	></uc-cell>
  </div>
</template>

<script>
import UcCell from '../components/uc-cell/uc-cell.vue'//
export default {
  name: 'column',
  data(){
    return {
     list:[]
    }
  },
  mounted(){
   this.$axios({
	url:'/api/column',
	params:{_limit:10,_pages:1}
   }).then(
	res=>this.list=res.data.data
   )
  },
  components: {//
   UcCell
  },
  methods:{//
   // show(ev){
   // console.log(ev)
   // }
  }
}
</script>

